import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl } from '@angular/forms';
import { HttpService } from '../../../services/http.service';
import * as moment from 'moment';
import { MatSort, MatTableDataSource } from '@angular/material';
import { GrowPosition } from '../../../model/data/growposition';
import { Products } from '../../../model/data/productconstants';


@Component({
  selector: 'app-growposition',
  templateUrl: './growposition.component.html',
  styleUrls: ['./growposition.component.css']
})
export class GrowpositionComponent implements OnInit {
  products: Array<string> = [];
  product: string;
  startDate: Date = new Date();
  endDate: Date = new Date();
  displayedColumns: string[] = ['position1', 'total1', 'count1', 'total2', 'count2', 'round'];
  dataSource: MatTableDataSource<GrowPosition>;
  @ViewChild(MatSort) sort: MatSort;

  constructor(private httpService: HttpService) { }

  ngOnInit() {
    Object.values(Products).forEach(pr => this.products.push(pr));
    this.product = Products.ALL;
  }

  getData() {
    const start = moment(this.startDate).format('YYYY-MM-DD');
    const end = moment(this.endDate).format('YYYY-MM-DD');
    this.httpService.getGrowsPositions(this.product, start, end).subscribe(
      result => {
        this.dataSource = new MatTableDataSource(result.data);
        this.dataSource.sort = this.sort;
        console.log(this.dataSource);
      }
    );
  }

  applyFilterCount(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
}


