import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl } from '@angular/forms';
import { HttpService } from '../../../services/http.service';
import * as moment from 'moment';
import { MatSort, MatTableDataSource } from '@angular/material';
import { GrowPosition } from '../../../model/data/growposition';
import { Products } from '../../../model/data/productconstants';
import { TaskContent } from '../../../model/data/taskcontent';
import { ModeItem } from '../../../model/data/mode';



@Component({
  selector: 'app-taskcontent',
  templateUrl: './taskcontent.component.html',
  styleUrls: ['./taskcontent.component.css']
})
export class TaskcontentComponent implements OnInit {
  products: Array<string> = [];
  product: string;
  subcategories: any[];
  subcategory: string;
  positions: any[];
  position: string;
  hardwares: any[];
  hardware: string;
  startDate: Date = new Date();
  endDate: Date = new Date();

  displayedColumns: string[] = ['task_id', 'title', 'description', 'decision', 'subcategory', 'position', 'hardware', 'result'];
  dataSource: MatTableDataSource<TaskContent>;
  modes = ModeItem.getModes();
  mode = this.modes[1].value;
  @ViewChild(MatSort) sort: MatSort;

  constructor(private httpService: HttpService) { }

  ngOnInit() {
    Object.values(Products).forEach(pr => this.products.push(pr));
    this.product = '';
    this.getHardware();
  }

  getData() {
    console.log('getData');
    const start = moment(this.startDate).format('YYYY-MM-DD');
    const end = moment(this.endDate).format('YYYY-MM-DD');
    this.httpService.getTaskContent(this.product, this.mode, start, end, this.subcategory, this.position, this.hardware).subscribe(
      result => {
        this.dataSource = new MatTableDataSource(result.data);
        this.dataSource.sort = this.sort;
        console.log(this.dataSource);
      }
    );
  }

  applyFilterCount(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }


  getSubcategories() {
    this.subcategories = null;
    this.subcategory = null;
    this.positions = null;
    this.position = null;
    this.httpService.getAttrSubcat(this.product).subscribe(
      result => this.subcategories = result.data
    );
  }

  getPositions() {
    this.positions = null;
    this.position = null;
    this.httpService.getAttrPositions(this.product, this.subcategory).subscribe(
      result => this.positions = result.data
    );
  }

  getHardware() {
    console.log('getHardware');
    this.httpService.getAttrHardware(this.product, this.subcategory, this.position).subscribe(
      result => {
        this.hardwares = result.data;
        this.hardwares.unshift(null);
      }
    );
  }
}


