import { Component, OnInit, AfterViewInit, OnDestroy } from '@angular/core';
import { AmChartsService, AmChart } from '@amcharts/amcharts3-angular';
import { HttpService } from '../../services/http.service';
import { Periods } from '../../model/http/periods';
import { Products } from '../../model/data/productconstants';
import { ModeItem } from '../../model/data/mode';
import { WorkingDays } from '../../model/data/workdays';
import { CallsDay } from '../../model/data/callsday';


@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit, AfterViewInit, OnDestroy {
  periods = Periods;
  period = this.periods.DAY;
  private chart: AmChart;
  modes = ModeItem.getModes();
  mode = this.modes[1].value;
  workingdays = WorkingDays.getDays();
  day = this.workingdays[1].value;
  callsday = CallsDay.getDays();
  callscount = this.callsday[1].value;

  constructor(private AmCharts: AmChartsService, private httpService: HttpService) { }

  ngOnInit() {
    this.getData();
  }



  ngAfterViewInit() {
    this.chart = this.AmCharts.makeChart('main',
      {
        "type": "serial",
        "legend": {
          "useGraphSettings": true,
          "valueAlign": "left",
          "position": "left"
        },
        "synchronizeGrid": true,
        "theme": "light",
        "marginRight": 40,
        "marginLeft": 40,
        "autoMarginOffset": 20,
        "mouseWheelZoomEnabled": false,
        "dataDateFormat": "YYYY-MM-DD",
        "valueAxes": this.initAxis(),
        "balloon": {
          "borderThickness": 1,
          "shadowAlpha": 0
        },
        "graphs": this.initGraph(),
        "chartScrollbar": {
          "graph": "g1",
          "oppositeAxis": false,
          "offset": 30,
          "scrollbarHeight": 80,
          "backgroundAlpha": 0,
          "selectedBackgroundAlpha": 0.1,
          "selectedBackgroundColor": "#888888",
          "graphFillAlpha": 0,
          "graphLineAlpha": 0.5,
          "selectedGraphFillAlpha": 0,
          "selectedGraphLineAlpha": 1,
          "autoGridCount": true,
          "color": "#AAAAAA"
        },
        "chartCursor": {
          "pan": true,
          "valueLineEnabled": true,
          "valueLineBalloonEnabled": true,
          "cursorAlpha": 1,
          "cursorColor": "#258cbb",
          "limitToGraph": "g1",
          "valueLineAlpha": 0.2,
          "valueZoomable": true
        },
        "valueScrollbar": {
          "oppositeAxis": false,
          "offset": 50,
          "scrollbarHeight": 10
        },
        "categoryField": "date",
        "categoryAxis": {
          "parseDates": true,
          "dashLength": 1,
          "minorGridEnabled": true
        },
        "export": {
          "enabled": true
        },
        "dataProvider": []
      }
    );
  }

  initAxis(): any {
    const result = [];
    Object.values(Products).forEach(val => {
      result.push({
        'id': val,
        'axisAlpha': 0,
        'position': 'left',
        'ignoreAxisWidth': true
      });
    }
    );
    return result;
  }

  initGraph(): any {
    const result = [];
    Object.values(Products).forEach(val => {
      result.push(
        {
          "valueAxis": val,
          "balloon": {
            "drop": true,
            "adjustBorderColor": false,
            "color": "#ffffff"
          },
          "bullet": "round",
          "bulletBorderAlpha": 1,
          "bulletColor": "#FFFFFF",
          "bulletSize": 3,
          "hideBulletsCount": 50,
          "lineThickness": 2,
          "title": val,
          "hidden": true,
          "useLineColorForBulletBorder": true,
          "valueField": val,
          "balloonText": `<span style='font-size:18px;'>[[value]]</span>`
        }
      );
    });
    return result;
  }

  ngOnDestroy() {
    if (this.chart) {
      this.AmCharts.destroyChart(this.chart);
    }
  }

  getData() {
    console.log(this.period, this.mode, this.day, this.callscount);
    this.httpService.getProducts(this.period, this.mode, this.day, this.callscount).subscribe(
      result => {
        if (result.data) {
          console.log(result);

          this.AmCharts.updateChart(this.chart, () => {
            this.chart.dataProvider = result.data;
          });

        }
      }
    );
  }

  onToggle(event: any) {
    this.mode = event.value;
    this.getData();
  }

  onToggleCb(event: any) {
    this.day = event.value;
    this.getData();
  }

  onToggleCalls(event: any) {
    this.callscount = event.value;
    console.log(this.callscount);
    this.getData();
  }
}
