export class WorkingDays {
    private static days = [
        ['all', ' '],
        ['working', ' ']
    ];

    value: string;
    label: string;
    checked: boolean;

    constructor(value: any, label: any, checked?: boolean) {
        this.value = value;
        this.label = label;
        this.checked = checked ? checked : false;
    }

    public static getDays(): Array<WorkingDays> {
        const result = new Array<WorkingDays>();
        this.days.forEach(arr =>
            result.push(new WorkingDays(arr[0], arr[1])));
        return result;
    }
}
