import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { Periods } from '../model/http/periods';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Result } from '../model/http/result';
import { Observable } from 'rxjs';
import { Products } from '../model/data/productconstants';
import { post } from 'selenium-webdriver/http';

@Injectable({
  providedIn: 'root'
})
export class HttpService {
  private baseUrl = environment.http_type + '://' + environment.host + ':' + environment.port;
  private REST_API = {
    products: this.baseUrl + '/api/products',
    product: this.baseUrl + '/api/product',
    subcategories: this.baseUrl + '/api/subcategories',
    position: this.baseUrl + '/api/position',
    table: this.baseUrl + '/api/table',
    attributes: this.baseUrl + '/api/attributes'
  };

  private reportId = {
    taskContent: 'taskcontent',
    hardWareData: 'hardWareData'
  };

  private httpOptions = {
    headers: {
      'Content-Type': 'application/json; charset=utf-8',
    }
  };

  constructor(private http: HttpClient) { }


  getProducts(period: Periods, mode: string, day: string, callscount: string): Observable<Result> {
    return this.http.get<Result>(
      this.REST_API.products + '?' + 'period=' + period + '&mode=' + mode + '&day=' + day + '&callscount=' + callscount);
  }


  getProduct(product: string, period: string, mode: string, day: string, callscount: string) {
    return this.http.get<Result>(
      this.REST_API.product + '?' + 'product=' + product + '&period=' + period + '&mode=' + mode + '&day=' + day + '&callscount=' + callscount);
  }

  getSubcategories(product: string) {
    return this.http.get<Result>(
      this.REST_API.subcategories + '?' + 'product=' + product);
  }

  getPositions(product: string, subcategory: string, period: string, mode: string, day: string, callscount: string) {
    return this.http.get<Result>(
      this.REST_API.position + '?' + 'product=' + product + '&subcategory=' + subcategory + '&period=' + period + '&mode=' + mode + '&day=' + day + '&callscount=' + callscount);
  }

  getGrowsPositions(product: string, startDate: string, endDate: string) {
    return this.http.get<Result>
      (
        this.REST_API.table + '?' + 'id=growposition' + '&product=' + product + '&startDate=' + startDate + '&endDate=' + endDate
      );
  }

  getTaskContent(product: string, mode: string, startDate: string, endDate: string, subcategory?: string, position?: string, hardware?: string) {
    const filter_product = product ? '&product=' + product : '';
    const filter_subcategory = subcategory ? '&subcategory=' + subcategory : '';
    const filter_position = position ? '&position=' + position : '';
    const filter_hardware = hardware ? '&hardware=' + hardware : '';
    return this.http.get<Result>
      (
        this.REST_API.table + '?' + 'id=' + this.reportId.taskContent + '&product=' + product + '&mode=' + mode + '&startDate=' + startDate + '&endDate=' + endDate + filter_subcategory + filter_position + filter_hardware
      );
  }

  getAttrSubcat(product: string): Observable<Result> {
    return this.http.get<Result>
      (
        this.REST_API.attributes + '?' + 'id=subcategory' + '&product=' + product
      );
  }

  getAttrPositions(product: string, subcategory: string) {
    return this.http.get<Result>
      (
        this.REST_API.attributes + '?' + 'id=position' + '&product=' + product + '&subcategory=' + subcategory
      );
  }

  getAttrHardware(product?: string, subcategory?: string, position?: string) {
    const filter_product = product ? '&product=' + product : '';
    const filter_subcategory = subcategory ? '&subcategory=' + subcategory : '';
    const filter_position = position ? '&position=' + position : '';
    return this.http.get<Result>
      (
        this.REST_API.attributes + '?' + 'id=hardware' + filter_product + filter_subcategory + filter_position
      );
  }


  getHardwareData(period: string, mode: string, day: string, callscount: string, subcategory: string, position: string) {
    const filter_period = period ? '&period=' + period : '';
    const filter_mode = mode ? '&mode=' + mode : '';
    const filter_day = day ? '&day=' + day : '';
    const filter_callscount = callscount ? '&callscount=' + callscount : '';
    const filter_subcategory = subcategory ? '&subcategory=' + subcategory : '';
    const filter_position = position ? '&position=' + position : '';

    const self = this;
    return this.http.get<Result>
      (
        this.REST_API.table + '?' + 'id=hardWareData' + filter_period + filter_subcategory + filter_position + filter_mode + filter_callscount + filter_day
      );
  }
}
