import { Component, OnInit, AfterViewInit, OnDestroy, Pipe, PipeTransform } from '@angular/core';
import { AmChartsService, AmChart } from '@amcharts/amcharts3-angular';
import { HttpService } from '../../services/http.service';
import { Periods } from '../../model/http/periods';
import { Products } from '../../model/data/productconstants';
import { ModeItem } from '../../model/data/mode';
import { WorkingDays } from '../../model/data/workdays';
import { CallsDay } from '../../model/data/callsday';


@Component({
  selector: 'app-products',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.css']
})


export class ProductComponent implements OnInit, AfterViewInit, OnDestroy {
  periods = Periods;
  period: string;
  products: Array<string> = [];
  product: string;
  private chart: AmChart;
  modes = ModeItem.getModes();
  mode = this.modes[1].value;
  workingdays = WorkingDays.getDays();
  day = this.workingdays[1].value;
  callsday = CallsDay.getDays();
  callscount = this.callsday[1].value;
  subcategories;
  private chartId = 'product';

  constructor(private AmCharts: AmChartsService, private httpService: HttpService) { }

  ngOnInit() {
    Object.values(Products).forEach(pr => this.products.push(pr));
    console.log(this.products);
    //  ALL
    this.products.shift();
  }



  ngAfterViewInit() {
    this.chart = this.AmCharts.makeChart(this.chartId,
      {
        "type": "serial",
        "legend": {
          "useGraphSettings": true,
          "valueAlign": "left",
          "position": "left"
        },
        "synchronizeGrid": true,
        "theme": "light",
        "marginRight": 40,
        "marginLeft": 40,
        "autoMarginOffset": 20,
        "mouseWheelZoomEnabled": false,
        "dataDateFormat": "YYYY-MM-DD",
        "valueAxes": [],
        "balloon": {
          "borderThickness": 1,
          "shadowAlpha": 0
        },
        "graphs": [],
        "chartScrollbar": {
          "graph": "g1",
          "oppositeAxis": false,
          "offset": 30,
          "scrollbarHeight": 80,
          "backgroundAlpha": 0,
          "selectedBackgroundAlpha": 0.1,
          "selectedBackgroundColor": "#888888",
          "graphFillAlpha": 0,
          "graphLineAlpha": 0.5,
          "selectedGraphFillAlpha": 0,
          "selectedGraphLineAlpha": 1,
          "autoGridCount": true,
          "color": "#AAAAAA"
        },
        "chartCursor": {
          "pan": true,
          "valueLineEnabled": true,
          "valueLineBalloonEnabled": true,
          "cursorAlpha": 1,
          "cursorColor": "#258cbb",
          "limitToGraph": "g1",
          "valueLineAlpha": 0.2,
          "valueZoomable": true
        },
        "valueScrollbar": {
          "oppositeAxis": false,
          "offset": 50,
          "scrollbarHeight": 10
        },
        "categoryField": "date",
        "categoryAxis": {
          "parseDates": true,
          "dashLength": 1,
          "minorGridEnabled": true
        },
        "export": {
          "enabled": true
        },
        "dataProvider": []
      }
    );
  }

  initAxis(): any {
    const result = [];
    this.subcategories.forEach(val => {
      result.push({
        'id': val.subcategory,
        'axisAlpha': 0,
        'position': 'left',
        'ignoreAxisWidth': true
      });
    }
    );
    console.log(result);
    return result;
  }

  initGraph(): any {
    const result = [];
    this.subcategories.forEach(val => {
      result.push(
        {
          "valueAxis": val.subcategory,
          "balloon": {
            "drop": true,
            "adjustBorderColor": false,
            "color": "#ffffff"
          },
          "bullet": "round",
          "bulletBorderAlpha": 1,
          "bulletColor": "#FFFFFF",
          "bulletSize": 3,
          "hideBulletsCount": 50,
          "lineThickness": 2,
          "title": val.subcategory,
          "hidden": true,
          "useLineColorForBulletBorder": true,
          "valueField": val.subcategory,
          "balloonText": `<span style='font-size:18px;'>[[value]]</span>`
        }
      );
    });
    console.log(result);
    return result;
  }

  ngOnDestroy() {
    if (this.chart) {
      this.AmCharts.destroyChart(this.chart);
    }
  }

  getData() {
    console.log(this.product, this.mode, this.day, this.callscount);
    this.httpService.getProduct(this.product, this.period, this.mode, this.day, this.callscount).subscribe(
      result => {
        if (result.data) {
          this.AmCharts.updateChart(this.chart, () => {
            this.chart.dataProvider = result.data.data;
            console.log(this.subcategories);
            if (!this.subcategories) {
              this.subcategories = result.data.attr;
              this.chart.graphs = this.initGraph();
            }
          });
        }
      }
    );
  }

  onToggle(event: any) {
    this.mode = event.value;
    this.getData();
  }

  onToggleCb(event: any) {
    this.day = event.value;
    this.getData();
  }

  onToggleCalls(event: any) {
    this.callscount = event.value;
    console.log(this.callscount);
    this.getData();
  }

  onToggleProduct() {
    this.subcategories = null;
    this.getData();
  }
}

