import { Component, OnInit, AfterViewInit, OnDestroy } from '@angular/core';
import { ModeItem } from 'src/app/model/data/mode';
import { Products } from 'src/app/model/data/productconstants';
import { HttpService } from 'src/app/services/http.service';
import * as moment from 'moment';
import { Periods } from 'src/app/model/http/periods';
import { WorkingDays } from 'src/app/model/data/workdays';
import { CallsDay } from 'src/app/model/data/callsday';
import { Observable, pipe, merge, of } from 'rxjs';
import { Result } from 'src/app/model/http/result';
import { AmChartsService, AmChart } from '@amcharts/amcharts3-angular';


@Component({
  selector: 'app-hardware',
  templateUrl: './hardware.component.html',
  styleUrls: ['./hardware.component.css']
})
export class HardwareComponent implements OnInit, OnDestroy {
  private chart: AmChart;
  private product: Products = Products.SIP;
  subcategories: string[] = [null];
  subcategory: string;
  positions: any[];
  position: string;
  hardwares: any[];
  hardware: string;
  startDate: Date = new Date();
  endDate: Date = new Date();
  periods: any = Periods;
  period = this.periods.DAY;
  workingdays = WorkingDays.getDays();
  day = this.workingdays[1].value;
  modes = ModeItem.getModes();
  mode = this.modes[1].value;
  callsday = CallsDay.getDays();
  callscount = this.callsday[1].value;
  private data: any = [];

  constructor(private AmCharts: AmChartsService, private httpService: HttpService) { }

  ngOnInit() {
    this.getData();
    this.getSubcategories();
  }

  getData() {
    console.log('getData');
    this.httpService.getHardwareData(this.period, this.mode, this.day, this.callscount, this.subcategory, this.position).subscribe(
      rusult => {
        this.data = rusult.data;
        console.log(this.data);
        if (!this.chart) {
          this.initChart();
        } else {
          this.AmCharts.updateChart(this.chart, () => {
            this.chart.dataProvider = this.data.data;
          });
        }
      });
  }


  private getSubcategories() {
    this.httpService.getAttrSubcat(this.product).subscribe(
      result => this.subcategories = this.subcategories.concat(result.data)
    );
  }


  getPositions() {
    console.log('getPositions');
    this.positions = null;
    this.position = null;
    this.getData();
    if (this.subcategory) {
      this.httpService.getAttrPositions(this.product, this.subcategory).subscribe(
        result => { this.positions = [null].concat(result.data); }
      );
    }
  }


  onToggle(event: any) {
    this.mode = event.value;
    this.getData();
  }

  onToggleCb(event: any) {
    this.day = event.value;
    this.getData();
  }

  onToggleCalls(event: any) {
    this.callscount = event.value;
    console.log(this.callscount);
    this.getData();
  }


  initChart() {
    this.chart = this.AmCharts.makeChart('chartdiv',
      {
        "type": "serial",
        "legend": {
          "useGraphSettings": true,
          "valueAlign": "left",
          "position": "left",
          "marginLeft": 100,
          "autoMargins": false
        },
        "synchronizeGrid": true,
        "theme": "light",
        "marginRight": 40,
        "marginLeft": 40,
        "autoMarginOffset": 20,
        "mouseWheelZoomEnabled": false,
        "dataDateFormat": "YYYY-MM-DD",
        "valueAxes": this.initAxis(),
        "balloon": {
          "borderThickness": 1,
          "shadowAlpha": 0
        },
        "graphs": this.initGraph(),
        "chartScrollbar": {
          "graph": "g1",
          "oppositeAxis": false,
          "offset": 30,
          "scrollbarHeight": 80,
          "backgroundAlpha": 0,
          "selectedBackgroundAlpha": 0.1,
          "selectedBackgroundColor": "#888888",
          "graphFillAlpha": 0,
          "graphLineAlpha": 0.5,
          "selectedGraphFillAlpha": 0,
          "selectedGraphLineAlpha": 1,
          "autoGridCount": true,
          "color": "#AAAAAA"
        },
        "chartCursor": {
          "pan": true,
          "valueLineEnabled": true,
          "valueLineBalloonEnabled": true,
          "cursorAlpha": 1,
          "cursorColor": "#258cbb",
          "limitToGraph": "g1",
          "valueLineAlpha": 0.2,
          "valueZoomable": true
        },
        "valueScrollbar": {
          "oppositeAxis": false,
          "offset": 50,
          "scrollbarHeight": 10
        },
        "categoryField": "date",
        "categoryAxis": {
          "parseDates": true,
          "dashLength": 1,
          "minorGridEnabled": true
        },
        "export": {
          "enabled": true
        },
        "dataProvider": this.data.data
      }
    );
  }

  initAxis(): any {
    const result = [];
    result.push({
      'id': 'val',
      'axisAlpha': 0,
      'position': 'left',
      'ignoreAxisWidth': true
    });
    return result;
  }

  initGraph(): any {
    const result = [];
    this.data.models.forEach(val => {
      result.push(
        {
          "valueAxis": val,
          "balloon": {
            "drop": false,
            "adjustBorderColor": false,
            "color": "#ffffff"
          },
          "bullet": "round",
          "bulletBorderAlpha": 1,
          "bulletColor": "#FFFFFF",
          "bulletSize": 4,
          "hideBulletsCount": 50,
          "lineThickness": 2,
          "title": val,
          "hidden": true,
          "useLineColorForBulletBorder": true,
          "valueField": val,
          "balloonText": `<span style='font-size:12px;'>${val} [[value]]</span>`
        }
      );
    });
    return result;
  }

  ngOnDestroy() {
    if (this.chart) {
      this.AmCharts.destroyChart(this.chart);
    }
  }

}
