import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppComponent } from './app.component';
import { AmChartsModule } from '@amcharts/amcharts3-angular';
import { MainComponent } from './components/main/main.component';
import { HttpClientModule, HttpClientJsonpModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { MatSelectModule } from '@angular/material/select';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatRadioModule } from '@angular/material/radio';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { ProductComponent } from './components/product/product.component';
import { SubcategoryComponent } from './components/subcategory/subcategory.component';
import { GrowpositionComponent } from './components/table/growposition/growposition.component';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatNativeDateModule, MatInputModule, MatSortModule, DateAdapter } from '@angular/material';
import { MatTableModule } from '@angular/material/table';
import { TaskcontentComponent } from './components/table/taskcontent/taskcontent.component';
import { MyDateAdapter } from './helper/mydateadapter';
import { MatButtonModule } from '@angular/material/button';
import { HardwareComponent } from './components/hardware/hardware.component';
import { MatToolbarModule } from '@angular/material/toolbar';
import { Block1Component } from './components/block1/block1.component';
import { Block2Component } from './components/block2/block2.component';
import { RouterModule, Routes } from '@angular/router';

const appRoutes: Routes = [
  { path: '', component: Block1Component },
  { path: 'block1', component: Block1Component },
  { path: 'block2', component: Block2Component },
  { path: '**', component: Block1Component }
];

@NgModule({
  declarations: [
    AppComponent,
    MainComponent,
    ProductComponent,
    SubcategoryComponent,
    GrowpositionComponent,
    TaskcontentComponent,
    HardwareComponent,
    Block1Component,
    Block2Component
  ],
  imports: [
    BrowserModule,
    AmChartsModule,
    HttpClientModule,
    HttpClientJsonpModule,
    MatSelectModule,
    BrowserAnimationsModule,
    MatRadioModule,
    FormsModule,
    ReactiveFormsModule,
    MatDatepickerModule,
    MatNativeDateModule,
    MatInputModule,
    MatTableModule,
    MatSortModule,
    MatButtonModule,
    MatToolbarModule,
    RouterModule.forRoot(
      appRoutes
    )
  ],
  providers: [{ provide: DateAdapter, useClass: MyDateAdapter }],
  bootstrap: [AppComponent]
})
export class AppModule { }
